.Device {
  -ms-flex-item-align: center;
      align-self: center;
  position: relative;
  background-size: 100% 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
          box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  margin: 2em 0;
}
@media (max-width: 980px) {
  .Device {
    zoom: 0.75;
  }
}
.Device.phone {
  margin: 0;
}
@media (max-width: 980px) {
  .Device.phone {
    margin: 2em 0;
  }
}
.Device > .screen {
  background-color: white;
}
.Device > .screen img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}
.Device > .screen > .embed-container {
  position: relative;
  padding-bottom: 56.25%;
  width: 100%;
  height: 0;
  overflow: hidden;
}
.Device > .screen > .embed-container iframe, .Device > .screen .embed-container object, .Device > .screen .embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.Device.iphone4_7 {
  width: 342px;
  height: 690px;
  padding: 88px 26px 88px 27px;
  background-image: url("./img/iphone4_7.png");
  -webkit-box-shadow: none;
          box-shadow: none;
}
.Device.iphone4_7 img {
  width: 289px;
  height: 514px;
}
.Device.windows-phone, .Device.windows-phone img {
  width: 300px;
  height: 500px;
}
.Device.web, .Device.web img {
  width: 1024px;
  height: 720px;
}
@media (max-width: 500px) {
  .Device.web, .Device.web img {
    width: 512px;
    height: 360px;
  }
}
.Device.video {
  max-width: 1024px;
}



