.Header {
  position: relative;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
}
.Header::after {
  content: "";
  background-image: url("./img/header-panel-bg.jpg");
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.Header > .buttons {
  position: absolute;
  padding: 0;
  right: 2em;
  top: 2em;
}
@media (max-width: 980px) {
  .Header > .buttons {
    right: 1em;
    top: 1em;
  }
}
.Header > .buttons > * {
  margin-left: 10px;
}
.Header > * {
  padding-top: 6em;
  padding-bottom: 8em;
}
.Header > * > .name {
  font-size: 3.75em;
  font-weight: 600;
  margin-bottom: 0.25em;
}
.Header > * > .title, .Header > * .twitter {
  font-size: 1.75em;
  font-weight: 300;
  line-height: 130%;
}
.Header > * > .description {
  margin-top: 2.5em;
  font-size: 1.75em;
  font-weight: 300;
  line-height: 130%;
}
.Header > .container {
  width: 480px;
  max-width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



